nav {
    position: absolute;
    top: 0;
    width: 100%;
    height: 50px;
    background: linear-gradient(45deg, rgb(156,14,156), midnightblue);
}
.list {
    list-style-type: none;
    background: linear-gradient(45deg, rgb(156,14,156), midnightblue);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.items a {
    margin: 0 15px;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 22px;
    cursor: pointer;
    color: #f1f1f1;
}
.btn {
    display: none;
    position: absolute;
    right: 10px;
    top: 7px;
    padding: 5px;
    color: #000;
    font-size: 18px;
    cursor: pointer;
}
@media screen and (max-width: 500px) {
    .list {
        flex-direction: column;
        height: auto;
    }
    .items:nth-child(1) {
        border-top: 1px solid rgb(255,255,255,0.6);
        margin-top: 50px;
    }
    .items {
        height: 70px;
        width: 100%;
        border-bottom: 1px solid rgb(255,255,255,0.6);
        line-height: 70px;
        text-align: center;
    }
    .btn {
        display: block;
    }
}
